import { InjectionToken } from '@angular/core';
import { ModuleConfig } from '../interfaces/module-config.interface';
import { ConfigService } from '../services/config.service';

export const FOR_ROOT_OPTIONS_TOKEN = new InjectionToken<ModuleConfig>('forRoot() configuration');

export function ProvideConfig(config: ModuleConfig): ModuleConfig {
  const configService: ConfigService = new ConfigService(config);
  return configService;
}
