import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { GoogleAnalyticsEventTrackingService } from './services/google-analytics-event-tracking.service';
import { FOR_ROOT_OPTIONS_TOKEN, ProvideConfig } from './config/ga-config';
import { ConfigService } from './services/config.service';
import { ModuleConfig } from './interfaces/module-config.interface';


@NgModule({
  declarations: [],
  imports: [
    CommonModule
  ],
  providers: [
  ]
})
export class GoogleAnalyticsModule {
  static forRoot(config: ModuleConfig): ModuleWithProviders {
    return {
      ngModule: GoogleAnalyticsModule,
      providers: [
        GoogleAnalyticsEventTrackingService,
        {
          provide: FOR_ROOT_OPTIONS_TOKEN,
          useValue: config
        },
        {
          provide: ConfigService,
          useFactory: ProvideConfig,
          deps: [FOR_ROOT_OPTIONS_TOKEN]
        }
      ]
    };
  }
}
